'use client'

import { useState, useEffect } from 'react';
import { motion } from 'framer-motion';
import { Button, TextField, Radio, RadioGroup, FormControlLabel } from '@mui/material';
import { toast } from 'react-hot-toast';
import { ethers } from 'ethers';
import { Web3Provider } from '@ethersproject/providers'; // Correct import for Web3Provider

// Import your contract ABI (replace with actual path)
import CoinFlipABI from './CoinFlipABI.json';

export default function CoinFlipGame() {
  const [bet, setBet] = useState(1);
  const [side, setSide] = useState('heads');
  const [balance, setBalance] = useState(100);  // Simulating a balance (could be updated from the contract)
  const [isFlipping, setIsFlipping] = useState(false);
  const [result, setResult] = useState<'heads' | 'tails' | null>(null);
  const [error, setError] = useState('');
  const [provider, setProvider] = useState<Web3Provider | null>(null); // Fixed type
  const [contract, setContract] = useState<any>(null);
  const [gameId, setGameId] = useState<number | null>(null);

  // Connect to Ethereum and load contract
  useEffect(() => {
    if (window.ethereum) {
      const web3Provider = new Web3Provider(window.ethereum); // Use Web3Provider directly from ethers
      setProvider(web3Provider);

      // Get the signer for transactions (write operations)
      const signer = web3Provider.getSigner();

      // Contract address and initialization
      const contractAddress = "0xYourContractAddress";  // Replace with your deployed contract address
      const contractInstance = new ethers.Contract(contractAddress, CoinFlipABI, signer); // Pass signer for write operations
      setContract(contractInstance);
    } else {
      toast.error('Please install MetaMask to play the game');
    }
  }, []);

  const flipCoin = async () => {
    if (bet > balance) {
      setError('Insufficient balance for this bet');
      return;
    }

    setError('');
    setIsFlipping(true);
    setResult(null);

    try {
      const sideEnum = side === 'heads' ? 0 : 1; // CoinSide.HEADS = 0, CoinSide.TAILS = 1
      const tx = await contract.createGame(sideEnum);
      const txReceipt = await tx.wait();
      const newGameId = txReceipt.events?.[0]?.args?.gameId.toNumber();
      setGameId(newGameId);

      // Wait for the game to be completed (simulate this step)
      await completeGame(newGameId);
    } catch (error) {
      toast.error('Transaction failed');
      console.error(error);
    }
  };

  const completeGame = async (gameId: number) => {
    try {
      setIsFlipping(false);

      // For read-only calls (e.g., getGameResult), use the provider
      const game = await contract.connect(provider).getGameResult(gameId); // Use provider to interact for reads

      setResult(game.won ? 'heads' : 'tails'); // Update with result of the coin flip
      if (game.won) {
        setBalance(balance + bet);
        toast.success(`You won $${bet}. Your new balance is $${balance + bet}.`);
      } else {
        setBalance(balance - bet);
        toast.error(`You lost $${bet}. Your new balance is $${balance - bet}.`);
      }
    } catch (error) {
      toast.error('Error fetching game result');
      console.error(error);
    }
  };

  return (
      <div className="flex flex-col items-center justify-center min-h-screen bg-gradient-to-r from-purple-400 via-pink-500 to-red-500">
        <div className="p-8 bg-white rounded-lg shadow-xl w-full max-w-md">
          <h1 className="text-3xl font-bold text-center mb-6">Coin Flip Game</h1>

          {error && <div className="text-red-500 text-center mb-4">{error}</div>}

          <div className="mb-6 text-center">
            <motion.div
                className="w-32 h-32 rounded-full bg-yellow-400 mx-auto flex items-center justify-center"
                animate={{
                  rotateY: isFlipping ? 1800 : 0,
                  scale: isFlipping ? 1.2 : 1,
                }}
                transition={{ duration: 2 }}
            >
              {result && <div className="text-2xl font-bold">{result === 'heads' ? 'Heads' : 'Tails'}</div>}
            </motion.div>
          </div>

          <div className="space-y-4">
            <div>
              <TextField
                  label="Your Bet ($)"
                  type="number"
                  value={bet}
                  onChange={(e) => setBet(Math.max(1, parseInt(e.target.value)))}
                  fullWidth
                  inputProps={{ min: 1, max: balance }}
              />
            </div>

            <RadioGroup value={side} onChange={(e) => setSide(e.target.value)}>
              <FormControlLabel value="heads" control={<Radio />} label="Heads" />
              <FormControlLabel value="tails" control={<Radio />} label="Tails" />
            </RadioGroup>

            <Button variant="contained" onClick={flipCoin} disabled={isFlipping} fullWidth>
              {isFlipping ? 'Flipping...' : 'Flip Coin'}
            </Button>

            <div className="text-center text-xl font-semibold">Balance: ${balance}</div>
          </div>
        </div>
      </div>
  );
}
